  export default {
    data() {
      return {
        // form数据
        create: {
          topics: '',

          meeting_feature: 'common',
          meeting_feature_option: [
            {
              label: '网络会议',
              meeting_feature: 'net',
            },
            {
              label: '电话会议',
              meeting_feature: 'phone'
            },
            {
              label: '普通会议',
              meeting_feature: 'common'
            },
          ],

          meeting_level: 'common',
          meeting_level_option: [
            {
              label: '高级',
              meeting_level: 'high'
            },
            {
              label: '中级',
              meeting_level: 'middle'
            },
            {
              label: '普通',
              meeting_level: 'common'
            },
          ],

          content: '',
          host: '',
          master: '',
          participant: [],
          duration: 0,
          site: '',

          meeting_time: this.datetime,
        },

        time: '',
        date: '',
        sitesOption: [],

        //参与人员选择 dialog
        participantVisible: false,
        dept: [],
        deptProps: {
          id: 'id',
          children: 'children',
          label: 'name',
        },

        transferData: [],

        datePickerOption: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7;
          }
        },
      }
    },
    computed: {
      datetime: () => {
        return this.date + ' ' + this.time;
      }
    },

    methods: {
      //todo children的处理
      getDept() {
        let _this = this;
        // console.log(_this.treeData)
        if (_this.dept) {
          axios.get('/getDept', {})
            .then(function (response) {
              var data = response.data.dept;
              console.log(response.data)
              console.log('dept ====' + data)
              _this.dept = data;
            }).catch(function (error) {
            console.log(error);
          })
        }
      },

      getUsers(data) {
        let _this = this;
        console.log("employee data =" +  data.id) ;
        axios.post('/getUsers', { id: data})
          .then(function (response) {
            const data = response.data.users;
            console.log(data);
            const transferData = [];

            for (let i = 0; i < data.length; i++) {
              transferData.push({
                key: data[i].id,
                label: data[i].name,
              });
            }

            _this.transferData = transferData;

          }).catch(function (error) {
          console.log(error);
        });
      }
    },

    mounted: function () {
      this.getDept();
    },
    created: function () {

      //在create中 获取参与人员数据以及会议室数据
//            let _this = this;
//
//            axios.post('/getTableCategory', {})
//                .then(function (response) {
//                    var data = response.data;
//                    _this.create.meeting_feature_option = data.meeting_feature_option;
//                    _this.create.meeting_level_option = data.meeting_level_option;
//                }).catch(function (error) {
//                console.log(error);
//            });


    },

  }




